/* Progressbar */
.progressbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  counter-reset: step;
  margin: 2rem 0 4rem;
}

.progressbar::before,
.progress {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 4px;
  width: 100%;
  background-color: #dcdcdc;
  z-index: -1;
}

.progress {
  background-color: var(--primary-color);
  width: 0%;
  transition: 0.3s;
}

.progress-step {
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: #dcdcdc;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.progress-step::before {
  counter-increment: step;
  content: counter(step);
}

.progress-step::after {
  content: attr(data-title);
  position: absolute;
  top: calc(100% + 0.5rem);
  font-size: 0.85rem;
  color: #666;
}

.progress-step-active {
  background-color: var(--primary-color);
  color: #f3f3f3;
}

/* Form */
.form {
  background-color: #fff;
  width: 75%;
  height: 668px;
  margin: 0 auto;
  margin-top: 1%;
  box-shadow: 3px 4px 0px 0px rgba(0, 0, 0, 0.25);
  border-radius: 0.35rem;
  padding: 1.5rem;
}

.form-step {
  display: none;
  transform-origin: top;
  animation: animate 0.5s;
}

.form-step-active {
  display: block;
}

/* Button */
.btns-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.btn {
  padding: 0.75rem;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  color: #f3f3f3;
  text-align: center;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: 0.3s;
}

.btn:hover {
  box-shadow: 0 0 0 2px #fff, 0 0 0 3px var(--primary-color);
}

/* modal */

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Use the full height of the viewport */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 999;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
  backdrop-filter: blur(3px);
  position: fixed;
}


.modal-content {
  background: #fff;
  margin: auto; /* shorthand for margin-top: auto; margin-bottom: auto; */
  padding: 12px 24px;
  z-index: 4;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  width: fit-content;
}


.modal-content label {
  text-align: left;
  font-size: 12px;
} 

.modal-header {
  font-weight: 500;
  padding: 10px 0;
  font-size: 26px;
  width: 100%;
}

.modal-header .modal-close {
  float: right;
  font-size: 20px;
  background: #ffffff;
  border: 0;
  outline: 0;
  padding: 5px 8px;
  border: 1px solid #ffffff;
  border-radius: 5px;
