.thb{
    font-family: 'Tahoma';
    font-weight: bold;
}
.lc{
    font-family: 'Lucida Sans';
    font-style: italic;
}

img{
    width: 200px;
    height: 200px;
}
.container{
    position: relative;
    max-width:none;
    max-height: max-content;
    width: 100%;
    padding: 85px;
    margin: 0 15px;
    background-color: #fff;
    font-family: 'Tahoma';
}

.container header{
    position: relative;
}
.container .formLayout{
    position: relative;
    margin-top: 16px;
    min-height: 500px;
    background-color: #fff;
    overflow: hidden;
}

.formLayout label{
    text-align: left;
}

.formLayout .inputDetails{
    width: 320px;
    
}
.formLayout .inputDetails{ /*login page na input*/
    width: 320px;
}
.formLayout .form.second {
    width: 800px;
}
.container .formLayout input{
    background-color: #EDF0F2;
    border: 1px solid #8f8f8f;
    padding: 13px 10px 13px 10px;
}
.container .formLayout .form{
    position: absolute;
    background-color: #fff;
    transition: 0.3s ease;
}
.container .formLayout .form.second{
    opacity: 0;
    pointer-events: none;
    transform: translateX(100%);
}
.formLayout.secActive .form.second{
    opacity: 1;
    pointer-events: auto;
    transform: translateX(0);
}
.formLayout.secActive .form.first{
    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);
}
.container .formLayout .title{
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 500;
    margin: 6px 0;
    color: #333;
}
.container .formLayout .fields{

    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: stretch;
}
.container .formLayout .fields input{

    padding: 10px;
}
.container .formLayout button, .backBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    width: 100%;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 5px;
    margin: 25px 0;
    background-color: #505050;
    transition: all 0.3s linear;
    cursor: pointer;
}
.container .formLayout .btnText{
    font-size: 14px;
    font-weight: 400;
}
.formLayout button:hover{
    background-color: #333333;
}
.formLayout button i,
.formLayout .backBtn i{
    margin: 0 6px;
}
.formLayout .backBtn i{
    transform: rotate(180deg);
}
.formLayout .buttons{
    display: flex;
    align-items: center;
}
.formLayout .buttons button , .backBtn{
    margin-right: 14px;
}
.required{
    color: red;
}


.link-register{
    cursor: pointer;
    color: black;
    font-size: 14px;
    text-decoration: none;
}
.d-flex {
    display: flex;
  }
  
  .justify-content-center {
    justify-content: center;
  }

  .hollowBtn {
    border-radius: 8px;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-color: #D0D3D9;
    height: 38px;
    width: 100px;
    color: #5D6679;
    position: relative;
    cursor: pointer;
    text-decoration: none;
  }

/* Center the progress bar horizontally */
.progressBarContainer {
    display: flex;
    justify-content: center;
    
  }
  
  /* Style for the progress bar */
  .progressBar {
    width: 20%; /* Adjust the width as needed */
    height: 10px; /* Adjust the height as needed */
    background-color: #ededed; /* Background color of the progress bar */
    border-radius: 30px;
  }
  
  /* Style for the progress bar fill */
  .progressBarFill {
    height: 100%;
    background-color: #5D6679; /* Fill color of the progress bar */
    border-radius: 30px;
    transition: 0.3s ease;
    transform: translateX(0);
  }
  